.studioCard {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  border: 1px solid rgba(139, 139, 139, 0.5);
  /* background: rgba(250,250,250,0.6); */
  border-radius: 0.7rem;
  margin: 1rem;
  width: 18rem;
  height: 25rem;
  padding: 1rem;
  box-shadow: 2px 5px 10px 0px #a7a7a7a6;
  transition: all 0.5s;
  background: linear-gradient(180deg, rgba(255,255,255,1), rgba(254, 254, 254, 0.5));
}

.studioCard:hover{
  /* background: #383838a6; */
  transform: scale(1.01, 1.01);
  transform: translate(0, 10px);
  box-shadow: 1px 1px 50px 5px rgba(0,0,0,0.25);
}

.studioCard img {
  width: 12rem;
  height: 12rem;
  object-fit: cover;
  border-radius: 50%;
}

.studioCard button {
  border-radius: 0.7rem;
  outline: none;
  margin: 0.5rem;
  height: 2.5rem;
  width: 10.2rem;
  background: linear-gradient(170deg,rgb(255, 255, 255), #789A9F);
  box-shadow: 1px 1px 3px 0px #a7a7a7a6;
  font-size: 1.2rem;
  font-weight: 100;
  color: white;
}

.studioCard button:hover {
  background: linear-gradient(160deg, rgb(255, 255, 255), #789a9f);
  cursor: pointer;
}

.horizontal-buttons{
  display: flex;
}

.studioCard h2 {
  margin: 0.45rem;
}
