.createProject{
  width: 100vw;
  margin: 2rem 0;
  border: 1px solid rgba(139, 139, 139, 0.5);
  border-radius: 0.7rem;
  box-shadow: 2px 5px 10px 0px #a7a7a7a6;
  width: 70%;
  max-width: 25rem;
  border-radius:0.7rem;
  /* padding: 0.5rem; */
}

.createProject h3 {
  margin: 1.5rem 0;
}

.horizontal-form {
  display: flex;
  justify-content: center;
  width: 95%;
}

.create-project-form{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.create-project-form input{
  width: 50%;
  height: 2rem;
  font-size: 1.2rem;
  font-weight: 100;
  border-radius: 0.3rem;
  outline: none;
  margin: 0.3rem 0.25rem;
  /* color: white; */
}

.create-project-form textarea {
  width: 92%;
  height: 5rem;
  font-size: 1.2rem;
  font-weight: 100;
  border-radius: 0.3rem;
  outline: none;
  border-color: rgba(139, 139, 139, 0.5);
  margin: 0.1rem 0.5rem;
}

.create-project-form input[type="submit"] {
  border-radius: 0.7rem;
  outline: none;
  margin: 1rem;
  height: 2.5rem;
  width: 10.2rem;
  background: linear-gradient(170deg,rgb(255, 255, 255), #789A9F);
  box-shadow: 1px 1px 3px 0px #789a9fa6;
  font-size: 1.2rem;
  font-weight: 100;
}

.createProject {
  position: fixed;
  top: 30%;
  transition: 0.5s all ease;
  background: rgb(255, 255, 255);
}

.createProject.hidden {
  top: -100%
}

.transparent-background{
  position: fixed;
  height: 100vh;
  width: 100vw;
}

